.projectDetail {
  background: #fff;
  width: 100%;
  min-height: 100vh;

  .newHeader {
    position: static;
  }

  .projectDetailContent {
    padding: 24px;
  }

  .content {
    margin: 40px 0;
  }

  .banner {
    height: 60vh;

    .silderBanner {
      height: 100%;
      position: relative;

      .slick-arrow {
        z-index: 10000;


        &::before {
          font-size: 30px;
          color: rgba(100, 100, 100, 0.603);
        }
      }

      .slick-prev {
        left: 10px !important;
      }

      .slick-next {
        right: 20px !important;
      }

      .slick-dots {
        position: absolute;
        bottom: 10px;

        li {

          width: 20px;
          height: 4px;
          margin: 0 10px;

          // height: 4px;
          // background-color: #d0d0d0;
          button {
            width: 25px;
            height: 4px;

            &::before {
              content: "";
              width: 25px;
              height: 4px;
              background: #f3f3f3db;
            }
          }
        }
      }

      .slick-list {
        height: 100%;

        .slick-track {
          height: 100%;
        }

        .slick-slide {
          height: 100%;

          .silderItem {
            height: 100%;

            img {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }
  }
}
